.App {
padding-left:3%;
}
.bg-app{
  background: green;
}
.logo{
  width: 70%
}
.bg-primary{
  background: #367BF5;
  background-color: #367BF5;
}
.bg-color{
  background:white;
}

h1{
  font-size: 65px;
  font-family: "Avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}
h2{
  font-size: 52px;
  font-family: "Avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}
.sub_nombres{
  font-size: 40px;
    font-family: "Avenir";
    white-space:pre-wrap;
    white-space:-moz-pre-wrap;
    white-space: -o-pre-wrap;
}
h3{
  font-size: 36px;
    font-family: "Avenir";
    white-space:pre-wrap;
    white-space:-moz-pre-wrap;
    white-space: -o-pre-wrap;
}
h4{
  font-size: 32px;
    font-family: "Avenir";
    white-space:pre-wrap;
    white-space:-moz-pre-wrap;
    white-space: -o-pre-wrap;
}
h5{
  font-size: 24px;
  line-height: 28,13px;
  font-family: "Avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}

.texto{
  font-size: 20px;
  line-height: 23,44px;
  font-family: "Avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}


.nombres{
  font-size: 16px;
  line-height: 19px;
  font-weight: bold;
  font-family: "Avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}
.subtitulos{
  font-size: 14px;
  line-height: 16px;
  font-weight:normal;
  font-family: "Avenir";
}
.nav-text{
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  font-weight: normal;
  color:black;
  font-family: "Avenir";

}


.nav-text-contacto{
  border-radius:12px;
  padding: 9px 14px 0px 14px;

}


#contacto{
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin: 10px 0px;
}

.text-gray{
  color:#5E6366;
}
.text-black{
  color:#3E4345;
}
.bg-dark{
  background-color: #000000;
  background: #000;
}
.idioma{
  background: #FFFFFF;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.12);
  border-radius: 12px;
}
.select{
margin-left: -3%;
width: 80%;
background: #FFFFFF;
box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.12);
border-radius: 12px;
border:1px solid black;
font-size: 14px;
}
.menu-inf{
  font-size: 18px;
}
/* ------------------------------------- MD  ------------------------------------------ */

@media  (max-width: 991px) {
  .bg-app{
    background: blue;
  }
  h1{
    font-size: 61px;
  }
  h2{
    font-size: 48px;
  }
  .sub_nombres{
    font-size: 36px;
  }
  h3{
    font-size: 32px;
  }
  h4{
    font-size: 28px;
  }
  h5{
    font-size: 20px;
    line-height: 28,13px;
  }

  .texto{
    font-size: 16px;
    line-height: 23,44px;
  }


  .nombres{
    font-size: 12px;
    line-height: 13px;
    font-weight: bold;
  }
  .subtitulos{
    font-size: 10px;
    line-height: 14px;
    font-weight:normal;
  }

  .nav-text{
    font-size: 16px;
    line-height: 19px;
  }

  .logo{
    width: 70%
  }

  .posicion{
    display: flex;
    flex-direction: row;
  }

}


/* ------------------------------------- SM  ------------------------------------------ */
@media (max-width:767px){
  .space-card{
    margin-bottom: 5%;
  }
  .logo{
    width: 80%
  }

  .bg-app{
    background: cyan;
  }
  .bg-color{
    background:#3E4345;
  }

  /**/
  h1{
    font-size: 36px;
  }
  /**/
  h2{
    font-size: 36px;
  }
  .sub_nombres{
    font-size: 40px;
  }
  /**/
  h3{
    font-size: 18px;
  }
  /**/
  h4{
    font-size: 18px;
  }
  /**/
  h5{
    font-size: 18px;
    line-height: 21px;
  }
  /**/
  .texto{
    font-size: 16px;
    line-height: 29px;
  }
  .nav-text{
    font-size: 16px;
    line-height: 19px;
    color:white;
    text-align: left;
  }

}
