.bg{
  background-image: url("../Img/iphone_BG.png");
  background-color: #cccccc;
  /* Full height */
  height: 800px;

  /* Center and scale the image nicely */
  background-position: 0px, 50px;
  background-repeat: no-repeat;
  background-size: cover;

}
.bg-responsive{
  padding-top: 10%;
  padding-bottom: 10%;
}


.grad{
  height : 5px;
  background-color: white;
  background-image: linear-gradient(#C6CACC,white);
}
.info{
  margin-top: 15%;
}
.subscripcion{
  background: linear-gradient(180deg, #9BAAFA 0%, #367BF5 100%);
  border-radius: 12px;
  text-align: center;
}
.opinion{
  padding-left: 8%;
  padding-right: 8%;
}
#placeholder {
  content:"holasdfsdf";
}
#placeholder:hover{

}
.secondRow{
  padding-top:10% ;
  padding-bottom: 10%;
}
#thirdRow{
  padding-top:5% ;
  padding-bottom: 5%;
  background-color: black;
}
#fourthRow{
    padding-top:5% ;
    padding-bottom: 5%;
}
#saludo{
  font-weight:bold;
}
#IoT{
  font-size: 65px;
  line-height: 76px;
  font-weight: bold;
}
#leer{
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin:10px 51px;
}
#buttonContacto{
  border-radius:12px;
  box-shadow:0px 10px 10px rgba(54, 123, 245, 0.18);
  padding: 9px 20px;
}
.resaltar:hover {
  -webkit-transform: scale(1.05);
   -ms-transform: scale(1.05);
   transform: scale(1.05);
}
.remark:hover{
  -webkit-transform: scale(1.05);
   -ms-transform: scale(1.05);
   transform: scale(1.05);
   color:white;

}
.bg-remark:hover{
background: black;
}

.btn-suscribete{
  position:absolute;
  right:10px;
  top:15px;
  borderRadius:12px;
}
.bg-visible{
  visibility: hidden;
  display: none;
}


.space{
  margin: 0%;
}
.COO{
  margin-top:0%;
}
.div-input{
  width: 100%;
}
.input-text{
  width: 90%;
  border-radius: 12px;
  position: relative;
}
.btn-susb{
  position:absolute;
  right:8%;
  bottom:53%;

}
.btn{
    border-radius: 12px;
}
::placeholder:hover:before{
  content:"hola";
}
.label-input:hove:before{
  content:"Ingresa tu E-mail";
  position: absolute;
  bottom:31%;
  left:10%;
}
/* ------------------------------------- MD  ------------------------------------------ */
@media  (max-width: 991px) {
  .COO{

    margin-top: 12%;
    padding-top: 0%;
  }
  .CEO{
      margin-top: 10%;
  }

  .btn-suscribete{
    position:absolute;
    right:10px;
    top:15px;
    borderRadius:12px;
  }

  .space{
    margin: 10% 0%;
  }
  .secondRow{
    padding-top:10%;
    padding-bottom:10%;

  }
  #thirdRow{
    padding-top:5%;
    padding-bottom:5%;
    background-color: black;
  }
  .resaltar {
    width: 60%;
    margin:auto;
  }
  #fourthRow{
      padding:5% 5%;
  }

  #buttonContacto{
    border-radius:5px;
    padding: 9px 25px;
  }
  .bg-responsive{
    padding-top: 8%;
    padding-bottom: 0%;
    padding-left: 5%;
  }
  .bg{
    background-image: url("../Img/iphone_BG.png");
    background-color: white;
    /* Full height */
    height: 550px;

    /* Center and scale the image nicely */
    background-position: 10px  0px;
    background-repeat: no-repeat;
    background-size: cover;

  }
  .bg-visible{
    visibility: visible;
    display: grid;
  }



}


/* ------------------------------------- SM  ------------------------------------------ */
@media (max-width:767px){
  .bg-responsive{
    padding: 10% 0%
  }

  .secondRow{
    padding-top:10%;
    padding-bottom:10%;
  }
  .bg-visible{
    visibility: visible;
    display: grid;
  }
  .bg{
      background-color:white;
        background-image: url("");
          height: 100%;
  }


  .resaltar {
    display: flex;
    align-items: center;
    width: 100%;
  }

}
