.bg-jobs{
  .bg-conocenos{
    background-image: url("../Img/jobs/foto.png");
    background-color: #cccccc;
    /* Full height */

    /* Center and scale the image nicely */
    background-position: 0, 50px;
    background-repeat: no-repeat;
    background-size: cover;
  }

}

.mapa{
  position:absolute;
    z-index: 2;
  bottom:25%;
  left:70%;
  width:40%;
}
.circulo-azul {
  position: absolute;
  z-index: 1;
  top:-10%;
  left:75%;
	border-radius: 50%;
  border:1px solid #9BAAFA;
	display: flex;
  background: linear-gradient(180deg, #9BAAFA 0%, #367BF5 100%);

}
.circle {
  width: 20rem;
	height: 20rem;
}

@media (max-width:991px){
  .mapa{
    position:absolute;
    bottom:15%;
  }
  .circle {
    width: 15rem;
  	height: 15rem;
  }
}

@media(max-width:767px){
  .mapa{
    position:relative;
    bottom: 20%;
    left: 0%;
    width:100%;
  }
  .circulo-azul {
    position: relative;
    z-index: 1;
    top:0%;
    left:-30%;

  }
  .circle {
    width: 25rem;
  	height: 25rem;
  }


}
