.bg-conocenos{
  background-image: url("../Img/conocenos/Team.png");
  background-color: #cccccc;
  /* Full height */
  height: 800px;

  /* Center and scale the image nicely */
  background-position: 0, 50px;
  background-repeat: no-repeat;
  background-size: cover;
}
#principios{
  font-size: 14px;
  line-height: 16,41px;
  font-family: "avenir";
  white-space:pre-wrap;
  white-space:-moz-pre-wrap;
  white-space: -o-pre-wrap;
}
.eme{
  margin-left: -27%;

}
.erre{
  margin-left: -7%;
  margin-right: 2%;
}
.buttonEquipo{
  box-shadow: 0px 10px 10px rgba(54, 123, 245, 0.18);
  border-radius: 12px;
  padding: 9px 20px 0px 20px;
}
.espiral{
  position: absolute;
  top:0;
  left: 33%;
}
.div-equipo{
  width: 50%;
}
.circulo {
	border-radius: 50%;
  border:1px solid #C4C4C4;
	display: flex;
	justify-content: center;
	align-items: center;

}
.circulo-img{
     width: 50px;
     height: 50px;
  	border-radius: 50%;
    border:1px solid #E0EBFD;
  	display: flex;
  	justify-content: center;
  	align-items: center;
    background: #E0EBFD;

}

.circulo_1 {
  width: 298px;
  height: 298px;
}
.circulo_2 {
  width: 425px;
  height: 425px;
}
.circulo_3 {
  width: 555px;
  height: 555px;
}
.circulo_4 {
  width: 686px;
  height: 686px;
}

.equipo{
  background: linear-gradient(180deg, #9BAAFA 0%, #367BF5 100%);
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.12);
  border-radius: 12px;
  width: 220px;
  height: 64px;

}

.bienvenido{
margin-top: 33%;
padding: 5%;

}
.div-engranaje{
  margin-top:3%;
}
.engranajes{
  width: 100%;

}

/*---DAVID---*/
.tooltip-david {
  position: absolute;
   left: 20%;
  bottom:75%;
  display: inline-block;
  z-index: 2;

}

.tooltip-david .tooltiptext-david {
  visibility: hidden;
  width: 326%;
  background-color: #EEE;
  color: #5E6366;
  text-align: center;
  border-radius: 6px;
  padding: 5px 2px;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  bottom:60px;
  left: 60px;
}

.tooltip-david:hover .tooltiptext-david {
  visibility: visible;
}
